.profile__overlay-container {
  width: 120px;
  height: 120px;
  position: relative;
  margin-right: 28px;
}

.profile__overlay-container:hover .profile__overlay {
  opacity: 1;
  cursor: pointer;
}

@media screen and (max-width: 425px) {
  .profile__overlay-container {
    margin: 0;
  }
}
