.profile__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../../images/edit-avatar.png);
  transition: .5s ease-in-out;
  opacity:0;
}
